/* Estilos generales */
body {
  font-family: Arial, sans-serif;
  background-color: #f7f4f4;
  margin: 0;
  padding: 0;
}

h2 {
  color: #faf5f5;
  font-size: 24px;
  margin-bottom: 20px;
}

/* Estilos para el contenedor principal */
.App {
  text-align: center;
  padding: 20px;
}

.App-header {
  background-color: #282c34;
  padding: 80px;
  color: white;
  border-bottom: 4px solid #61dafb;
  min-height: 100vh;
  /* Hace que el <header> tenga al menos la altura de la ventana del navegador */
  box-sizing: border-box;
  /* Incluye el padding y border en el cálculo de la altura total */
}

/* Estilos para la navegación */
nav {
  width: 20%;
  /* O el ancho que desees */
  margin: 0 auto;
  /* Esto centra el <nav> horizontalmente */
}

nav ul {
  list-style-type: none;
  padding: 0;
  margin: 0;
  display: flex;
  justify-content: center;
}

nav ul li {
  margin: 0 15px;
}

nav ul li a {
  color: #0c0c0c;
  text-decoration: none;
  font-size: 18px;
}

nav ul li a:hover {
  text-decoration: underline;
}

/* Estilos para el botón de Log Out */
nav ul li button {
  color: #61dafb;
  background: none;
  border: none;
  cursor: pointer;
  font-size: 18px;
  padding: 0;
}

nav ul li button:hover {
  text-decoration: underline;
}

/* Estilos para la lista de tareas */
ul {
  list-style-type: none;
  padding: 0;
  margin: 20px auto;
  max-width: 600px;
}

ul li {
  background-color: #70d2f8;
  /* Color de fondo de cada tarea */
  padding: 20px;
  border: 1px solid #ddd;
  /* Borde alrededor de cada tarea */
  border-radius: 8px;
  /* Bordes redondeados */
  margin-bottom: 20px;
  /* Espacio entre tareas */
  box-shadow: 0px 4px 8px rgba(0, 0, 0, 0.1);
  /* Sombra para profundidad */
}

ul li h3 {
  font-size: 20px;
  color: #333;
  margin-bottom: 10px;
}

ul li p {
  margin: 5px 0;
  color: #555;
}

/* Estilos para los formularios */
form {
  max-width: 400px;
  margin: 20px auto;
  background-color: white;
  padding: 20px;
  border-radius: 8px;
  box-shadow: 0px 4px 8px rgba(240, 2, 2, 0.1);
}

form div {
  margin-bottom: 15px;
  text-align: left;
}

form label {
  display: block;
  margin-bottom: 5px;
  color: #333;
}

form input {
  width: 95%;
  padding: 8px;
  border: 1px solid #ccc;
  border-radius: 4px;
}

form button {
  width: 100%;
  padding: 10px;
  background-color: #61dafb;
  color: white;
  border: none;
  border-radius: 4px;
  cursor: pointer;
  font-size: 16px;
}

form button:hover {
  background-color: #21a1f1;
}

/* Estilo para mensajes de error o éxito */
p {
  color: #e74c3c;
  font-size: 16px;
}

p.success {
  color: #2ecc71;
}

p.error {
  color: #e74c3c;
}